import "./styles.css";

function formatName(user) {
  return user.firstName + " " + user.lastName;
}

const user = {
  firstName: "Harper",
  lastName: "Perez"
};

export default function App() {
  return (
    <div className="App">
      <div class="element">Hello {formatName(user)}</div>
    </div>
  );
}
